@import "../../global.scss";

.menu {
    width: 300px;
    height: 100vh;
    background-color: $mainColor;
    position: fixed;
    top: 0;
    right: -300px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ;
    opacity: 0;

    &.active{
        right: 0;
        opacity: 1;
    }

    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 28px;
        font-weight: 300;
        color: white;
        letter-spacing: 2px;
        text-align: center;
        width: 60%;
        li{
            margin-bottom: 25px;
            a{
                font-size: inherit;
                color:inherit;
                text-decoration: none;
            }

            &:hover{
                
                font-weight: 400;
            }
        }
    }
}